// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

// import { BootstrapVue } from 'bootstrap-vue'

// Vue.use(BootstrapVue)

import VModal from 'vue-js-modal'

import LoadScript from 'vue-plugin-load-script'
Vue.use(VModal)
Vue.use(LoadScript)

Vue.config.productionTip = false

// Vue.loadScript("https://ajax.googleapis.com/ajax/libs/jquery/3.5.1/jquery.min.js")
// Vue.loadScript("/static/scripts/jquery.min.js")

// Vue.loadScript("https://cdnjs.cloudflare.com/ajax/libs/popper.js/2.6.0/umd/popper.min.js")
// Vue.loadScript("/static/scripts/popper.min.js")

// Vue.loadScript("https://maxcdn.bootstrapcdn.com/bootstrap/3.3.7/js/bootstrap.min.js")
// Vue.loadScript("/static/scripts/bootstrap.min.js")

// Vue.loadScript("https://code.jquery.com/ui/1.12.0/jquery-ui.min.js")
// Vue.loadScript("/static/scripts/jquery-ui.min.js")

Vue.loadScript('/static/scripts/ext_lib.js')

// https://stackoverflow.com/questions/36170425/detect-click-outside-element
Vue.directive('click-outside', {
    bind: function (el, binding, vnode) {
        el.clickOutsideEvent = function (event) {
            // here I check that click was outside the el and his children
            if (!(el == event.target || el.contains(event.target))) {
                // and if it did, call method provided in attribute value
                vnode.context[binding.expression](event)
            }
        }
        document.body.addEventListener('click', el.clickOutsideEvent)
    },
    unbind: function (el) {
        document.body.removeEventListener('click', el.clickOutsideEvent)
    }
})

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'

})



