<template>

	<div>

	    <modal name="camera_modal"  :adaptive=true width="600" height="auto" @before-close="" @opened="onStart">

	        <div class="container-fluid modal_zoom_80">
	            <div class="card" style="border:none">
	               <div class="header">
	                  <h4 class="title">Add data from camera</h4>
	                  <p class="category"></p>
	               </div>
	               <div class="content">

						<div class="container">
					        <div class="row">
					            <div class="col-md-12">
					                <div class="border">
					                    <vue-web-cam
					                        ref="webcam"
					                        :device-id="deviceId"
					                        width="100%"
					                        height="100%"
					                        @started="onStarted"
					                        @stopped="onStopped"
					                        @error="onError"
					                        @cameras="onCameras"
					                        @camera-change="onCameraChange"
					                    />
					                </div>

					                <div class="row">
					                    <div class="col-md-12">
					                        <select v-model="camera">
					                            <option>-- Select Device --</option>
					                            <option
					                                v-for="device in devices"
					                                :key="device.deviceId"
					                                :value="device.deviceId"
					                            >{{ device.label }}</option>
					                        </select>
					                    </div>
					                    <div class="col-md-12">
					                        <button type="button" class="btn btn-primary" @click="onCapture">Capture Photo</button>

					                    </div>
					                </div>
					            </div>
			<!-- 		            <div class="col-md-6">
					                <h2>Captured Image</h2>
					                <figure class="figure">
					                    <img :src="img" class="img-responsive" />
					                </figure>
					            </div> -->
					        </div>
					    </div>

	               </div>
	            </div>

	        </div>

	    </modal>

	</div>

</template>

<script type="text/javascript">

import { WebCam } from 'vue-web-cam'

export default {
		  name: 'CameraImport',
		  props: [ ],
		   components: {
		        'vue-web-cam': WebCam
		    },

		  data () {
		  	return {
		  		img: null,
	            camera: null,
	            deviceId: null,
	            devices: []
			  }
		  },

		  computed: {
		        device: function () {
		            return this.devices.find(n => n.deviceId === this.deviceId)
		        }
		    },
		    watch: {
		        camera: function (id) {
		            this.deviceId = id
		        },
		        devices: function () {
		            // Once we have a list select the first one
		            const [first, ...tail] = this.devices
		            if (first) {
		                this.camera = first.deviceId
		                this.deviceId = first.deviceId
		            }
		        }
		    },
		    methods: {
		        onCapture () {
		            this.img = this.$refs.webcam.capture()
		            this.$parent.addImgB64(this.img, undefined)
		        },
		        onStarted (stream) {
		            console.log('On Started Event', stream)
		        },
		        onStopped (stream) {
		            console.log('On Stopped Event', stream)
		        },
		        onStop () {
		            this.$refs.webcam.stop()
		        },
		        onStart () {
		            this.$refs.webcam.start()
		        },
		        onError (error) {
		            console.log('On Error Event', error)
		        },
		        onCameras (cameras) {
		            this.devices = cameras
		            console.log('On Cameras Event', cameras)
		        },
		        onCameraChange (deviceId) {
		            this.deviceId = deviceId
		            this.camera = deviceId
		            console.log('On Camera Change Event', deviceId)
		        }
		    }
}

</script>



