<template >

	<!-- <sometig>  -->
		<tr v-bind:class="[ $parent.selected ? 'selected_table' : '']" v-if="mode=='table'" >
			<td>
				<slot name="aaaa"></slot>
			</td>
			<td style="width:100%">
				<slot name="bbb"></slot>
			</td>

		</tr>

		<div v-else-if="mode=='gal'" class="gal_resp col-6  reppppp" style=" max-width:400px; padding: 8px">

	        <div v-bind:class="{ selected:$parent.selected }" class="card cardshdow" style="margin-bottom: 0px; border-radius: 12px;">
	        	<div style="">
	        		<slot name="aaa"></slot>
	        	</div>

	        	<div style="position:absolute ; top:10px; left:9px;" >
	        		<slot name="bbb"></slot>
	        	</div>

	        	<div style="position:absolute ; top:23px; right:19px;" >
	        		<slot name="ccc"></slot>
	        	</div>

	        </div>

	    </div>

	    <div v-else-if="mode=='gal_exp'" id="gal_item_expanded" class="col-12  reppppp" style="padding: 8px">

	    	<div style="height: calc( 120vh - 193px); overflow: auto;">
		        <div v-bind:class="{ selected_large:$parent.selected }" class="card cardshdow" style="margin-bottom: 0px; border-radius: 12px;">

		        	<div class="large_card">

		        		<slot name="aaa_big"></slot>
		        	</div>

		        	<div style="position:absolute ; top:10px; left:9px; zoom:1.5" >
		        		<slot name="bbb"></slot>
		        	</div>

		        	<div style="position:absolute ; top:23px; right:19px; zoom:1.5" >
		        		<slot name="ddd"></slot>
		        	</div>

		        </div>
	    	</div>

	    </div>

    <!-- </sometig> -->
</template>

<script>
export default {
    name: 'GalleryItemOuter',
    props: ['mode'],
    data () {
        return {
            // mode:'table'
        }
    }

}

</script>

<style type="text/css">

	.selected_table{
		background-color:#EEEFEE;
	}

</style>



