<template>

    <GalleryItemOuter   :mode=modew>

        <template   v-slot:aaa>
             <img v-on:click="click_card_body" style="padding: 0 ; max-width:100%; max-height:100%;" class="imgCardTop" :src="get_img_uri()">
        </template>

         <template   v-slot:aaa_big>
             <img v-on:click="click_card_body" style="padding: 0 ; max-width:100%; max-height:100%;" class="imgCardTop" :src="get_img_uri()">
             <div   style="width:100% ; height:100% ; position:absolute ; top:0 ; left:0">

              <div id="bounding_box_wrapper" style="position:relative; width:100% ; height:100% ; margin:auto; ">

              </div>

              <div v-click-outside="hide_bb_menu" id="bounding_box_dropdown" style="background-color: white ; position:fixed ; display: none; top:300px ; left:300px" >
                    <div style="min-width: 150px ; opacity:0.9 ; border:none" class="dd-mensu allign_left" aria-labelledby="dropdownMenuButtoxn">

                              <a class="dropdown-item" v-for="(tag, index) in $parent.tags" v-on:click="addTagBBox (tag)" href="#">{{tag}}</a>

                              <hr style="margin: 0">

                              <div  style="margin-bottom: 0px;" class="input-group form-group">
                                <input v-model="newTagInpBBox" v-on:keyup.enter="newTagBBox"  style=" border-radius: 4px 4px 4px 4px; border-bottom-color:rgb(227, 227, 227);  margin: 5px;font-size: 0.9rem;height: 30px; border-right: 1px solid;border-right-color:rgb(227, 227, 227);  " class="form-control" name="newtag" placeholder="New Tag">
                                <span v-on:click="newTagBBox" style="border: none; width: 30px; padding: 4px;" class=" text-muted input-group-addon"><i class="fa fa-plus"></i></span>
                              </div>

                              <!-- <hr style="margin: 0 ; ">
                              <a class="dropdown-item" v-on:click=" $parent.newTagInpWheretoAdd=gal_id; $parent.$modal.show('new_tag_modal');" href="#">New tag</a> -->
                        </div>

              </div>

             </div>
        </template>

        <template v-slot:aaaa>
             <img style="padding: 0 ; max-width:100px; max-height:100%;"   :src="get_img_uri()">
        </template>

        <template v-slot:ccc>
           <span   data-toggle="dropdown" v-on:click="deleteItem"  style="cursor: pointer;"   class="purpleTag delete_botton"> <i class="fa fa-trash"></i>  </span>

           <span   data-toggle="dropdown" v-on:click="expand_item"  style="cursor: pointer;"   class="purpleTag delete_botton"> <i class="fa fa-expand"></i>  </span>

        </template>

        <template v-slot:ddd>

           <span v-on:click="new_bounding_box"  style="cursor: pointer;"  class="purpleTag">Add box<i style=" margin-left:12px; cursor: pointer;"  class="fa fa-plus"></i></span>

           <span   data-toggle="dropdown" v-on:click="deleteItem"  style="cursor: pointer;"   class="purpleTag "> <i class="fa fa-trash"></i>  </span>

           <span   data-toggle="dropdown" v-on:click="$parent.expand_mode = false"  style="cursor: pointer;"   class="purpleTag "> <i class="fa fa-times"></i>  </span>

        </template>

           <!--  <div class="imgCardTop">
                <img :src="get_img_uri()">
            </div> -->

            <!-- v-click-outside="unclick_card_body"  -->
       <template v-slot:bbb>
            <div class="card-body ">

                    <span class="dropdown">

                        <span style="cursor: pointer;" data-toggle="dropdown"  v-if="$parent.gal_items[gal_id].tag"  class="purpleTag">{{ $parent.gal_items[gal_id].tag }}<i style=" margin-left:12px; cursor: pointer;"  class="fa fa-caret-down"></i></span>

                        <span   v-else data-toggle="dropdown"  style="cursor: pointer;"   class="purpleTag">Add tag
                            <!-- <i class="fa fa-tag"></i>  -->
                        </span>

                        <div style="min-width: 150px ; opacity:0.9 ; border:none" class="dd-menu allign_left" aria-labelledby="dropdownMenuButton">

                              <a class="dropdown-item" v-for="(tag, index) in $parent.tags" v-on:click="addTag(tag)" href="#">{{tag}}</a>

                              <hr style="margin: 0">

                              <div  style="margin-bottom: 0px;" class="input-group form-group">
                                <input v-model="newTagInp" v-on:keyup.enter="newTag"  style=" border-radius: 4px 4px 4px 4px; border-bottom-color:rgb(227, 227, 227);  margin: 5px;font-size: 0.9rem;height: 30px; border-right: 1px solid;border-right-color:rgb(227, 227, 227);  " class="form-control" name="newtag" placeholder="New Tag">
                                <span v-on:click="newTag" style="border: none; width: 30px; padding: 4px;" class=" text-muted input-group-addon"><i class="fa fa-plus"></i></span>
                              </div>

                              <!-- <hr style="margin: 0 ; ">
                              <a class="dropdown-item" v-on:click=" $parent.newTagInpWheretoAdd=gal_id; $parent.$modal.show('new_tag_modal');" href="#">New tag</a> -->
                        </div>

                    </span>

                   <!--  <span style="text-decoration-line: underline; float: right;"  class="text-muted">
                         <div class="dropdown">
                             <i style="" class="fa fa-ellipsis-v" data-toggle="dropdown" ></i>
                             <div class="dd-menu allign_right" aria-labelledby="dropdownMenuButton">
                                  <a class="dropdown-item" v-on:click="deleteItem" href="#">Delete</a>
                            </div>

                         </div>
                    </span>  -->

                    <!-- <Dropdown></Dropdown> -->

            </div>
      </template>
    </GalleryItemOuter>

</template>

<script>

import Dropdown from '@/components/Dropdown'

import DataMgr from '@/datamgr'
import GalleryItemOuter from '@/components/GalleryItemOuter'
import {ImageAnnotator, BBox} from 'annotate2d'

export default {
    name: 'GalleryItem2',
    props: ['gal_id', 'modew' ],
    data () {
        return {
            newTagInp: '',
            newTagInpBBox: '',
            selected: false,
            visabble: true,
            annotator: undefined,
            block_saving: false, // some bug bc the callback is called befrre destryig  the annotaotr elements
            jq: $, // so that stupid vue componetns can access jqeury like a
            cur_selected_bb: undefined // the last clicked bounding box so that dropdown knows where to add the data
        }
    },

    // mounted () {
    //       this.$modal.show('new_tag_modal')
    //  },

    components: {Dropdown, GalleryItemOuter },

    mounted: function () {
        if (this.modew == 'gal_exp') {
            this.init_bouding_box_ui()
        }
    },

    // executed when the supid vaiable changes
    watch: {
        gal_id: function () {
            this.init_bouding_box_ui()
        }
    },

    beforeDestroy: function () {
        this.dispose_annotator()
    },

    methods: {

        // when you click outside the item
        unclick_card_body: function () {
            const e = window.event

            if ((!this.$parent.expand_mode) && (e.shiftKey || e.metaKey || e.altKey || e.ctrlKey)) {

            } else {
                this.selected = false
                this.$parent.mouse_selected_items[ this.gal_id] = false
                this.$parent.update_mouse_select_count()
            }
        },

        dispose_annotator: function () {
            this.block_saving = true

            if (this.annotator) {
                this.annotator.onPropertyChanged = function () {}

                this.annotator.bBoxes.forEach(b => {
                    // b.onTextClicked = onTextClicked
                    b.onPropertyChanged = function () {}
                })
            }

            for (const ann of this.$parent.annotators) { ann.dispose() }
            this.$parent.annotators = []
            this.block_saving = false
        },

        new_bounding_box: function () {
            let box = this.annotator.addBbox({
                label: 'click me',
                color: '#ff3333',
                size: {x: 0.2, y: 0.2},
                position: {x: Math.random() * 0.6 + 0.2, y: Math.random() * 0.6 + 0.2},
                maxSize: {x: 1, y: 1},
                minSize: {x: 0.001, y: 0.001}
            })
            box.selectBox()
            box.onTextClicked = this.on_bounding_box_click
            box.onPropertyChanged = this.save_boundig_boxes
        },

        on_bounding_box_click: function (bb) {
            setTimeout(function () { $('#bounding_box_dropdown').show() }, 10)

            this.cur_selected_bb = bb

            var rect = bb.getViewportRect()
            $('#bounding_box_dropdown').css('top', rect.y + 30)
            $('#bounding_box_dropdown').css('left', rect.x)
        },

        hide_bb_menu: function () {
            $('#bounding_box_dropdown').hide()
        },

        addTagBBox: function (tag) {
            // this.$parent.gal_items[this.gal_id].tag = tag;
            this.cur_selected_bb.label = tag
            this.save_boundig_boxes()
            this.$parent.update_gall()
            this.hide_bb_menu()
        },

        newTagBBox: function () {
            this.$parent.newTag(this.newTagInpBBox)
            if (this.newTagInpBBox.trim() !== '') {
                this.addTagBBox(this.newTagInpBBox.trim())
                this.newTagInpBBox = ''
            }
        },

        save_boundig_boxes: function () {
            if (this.block_saving) { return }
            console.log('saveoo ' + this.gal_id)
            this.$parent.gal_items[ this.gal_id].bb_data = this.annotator.getBoxes()
            console.log(this.annotator.getBoxes())
        },

        init_bouding_box_ui: function () {
            let bb_data = this.$parent.gal_items[ this.gal_id].bb_data
            this.dispose_annotator()

            this.block_saving = true
            console.log('INIT ' + this.gal_id)

            for (let ann of this.$parent.annotators) { ann.dispose() }
            console.log('disposed ' + this.gal_id)
            this.$parent.annotators = []

            this.annotator = new ImageAnnotator('bounding_box_wrapper', '')
            this.$parent.annotators.push(this.annotator)
            this.annotator.stage.mouseScalingFactor = 1.25
            this.annotator.onPropertyChanged = this.save_boundig_boxes

            this.annotator.onImageClicked = (event, ann) => {
                console.log(event)
                // ann.toggleHitCanvas()
                // $('#bounding_box_dropdown').hide() // todo: divam: see this. To hide when clicked on empty area. add whatever checks here that you want.
            }

            if (bb_data) {
                console.log('loaded ' + this.gal_id)
                console.log(bb_data)
                this.annotator.addBoxes(bb_data)
                console.log('loaded  end' + this.gal_id)
            }

            let that = this

            this.annotator.bBoxes.forEach(b => {
                b.onTextClicked = that.on_bounding_box_click
                b.onPropertyChanged = that.save_boundig_boxes
            })

            this.block_saving = false
        },

        // the code to select the item .. when you click
        click_card_body: function () {
            if (this.just_selected) {
                this.expand_item()
                return
            }

            this.just_selected = true
            const thatt = this
            this.$parent.expand_img = this.gal_id
            setTimeout(function () { thatt.just_selected = false }, 200)

            this.selected = true

            const e = window.event

            // if shift key was hit .. hence select multiple items
            if ((!this.$parent.expand_mode) && e.shiftKey && this.$parent.prev_select && this.$parent.mouse_selected_items[ this.$parent.prev_select] && this.$parent.selected_idx.includes(this.$parent.prev_select)) {
                const i1 = this.$parent.selected_idx.indexOf(this.$parent.prev_select)
                const i2 = this.$parent.selected_idx.indexOf(this.gal_id)

                const isss = new Set()

                if (i1 < i2) {
                    for (var i = i1; i <= i2; i++) {
                        isss.add(this.$parent.selected_idx[i])
                    }
                } else {
                    for (var i = i2; i <= i1; i++) {
                        isss.add(this.$parent.selected_idx[i])
                    }
                }

                console.log(isss)

                for (let el of this.$parent.$children) {
                    if (el.gal_id != undefined) {
                        if (isss.has(el.gal_id)) {
                            this.$parent.mouse_selected_items[el.gal_id] = true
                            el.selected = true
                        }
                    }
                }
            }

            console.log(this.$parent.mouse_selected_items)

            this.$parent.prev_select = this.gal_id

            this.$parent.mouse_selected_items[this.gal_id] = true
            this.$parent.update_mouse_select_count()
        },

        // remove the tag of the item
        removeTag: function (event) {
            this.$parent.gal_items[this.gal_id].tag = undefined
            this.$parent.update_gall()
        },

        // assign the given tag to that item
        addTag: function (tag) {
            this.$parent.gal_items[this.gal_id].tag = tag
            this.$parent.update_gall()
        },

        // create a new tag  and assign it
        newTag: function () {
            const a = this.$parent.newTag(this.newTagInp)
            if (this.newTagInp.trim() !== '') {
                this.addTag(this.newTagInp.trim())
                this.newTagInp = ''
            }
        },

        // delete the item
        deleteItem: function () {
            delete this.$parent.gal_items[this.gal_id]
            delete this.$parent.mouse_selected_items[this.gal_id]
            this.visabble = false
            this.$parent.update_mouse_select_count()
            this.$parent.update_gall()
            // a hack to update the tags list lmao
            this.$parent.tags.push('jj')
            this.$parent.tags.pop()
        },

        expand_item: function () {
            this.$parent.expand_img = this.gal_id
            this.$parent.expand_mode = true
        },

        get_img_uri: function () {
            return DataMgr.image_b64[this.$parent.gal_items[this.gal_id].img]
        }

    }

}

</script>

<style type="text/css">

    .selected{
         /*   border-width: 2px;
            border-color: #6e47e2;
            border-color: #4ca4ff;*/
            box-shadow: 0px 0px 0 3px  #4ca4ff;
    }

    .delete_botton{
      display: none;
    }

    div:hover > div > .delete_botton{
      display: inline;
    }

canvas.docbox {
    width: 100px;
    height: 100px;

    padding: 10px;
}
.draggable{
    position: relative;
}
.resizable{
    width: 100px;
}

.ui-draggable{
  position: absolute !important;
      /*z-index: 20;*/
      background-color: rgba(255,0,0,0.1);
    /*opacity: 0.3;*/
    /*pointer-events: all*/
    border-width: 2px;
    border-color: red;
    border-style: solid;
}

.ui-resizable-nw, .ui-resizable-ne, .ui-resizable-sw, .ui-resizable-se, .ui-resizable-n, .ui-resizable-e, .ui-resizable-s, .ui-resizable-w {
    width: 12px;
    height: 12px;
    background-color: rgba(255,0,0,1);
    border: 1px solid #000000;

}

.ui-resizable-nw, .ui-resizable-ne, .ui-resizable-sw, .ui-resizable-se
{
  border-radius: 10px;
  width: 15px;
    height: 15px;
}

.ui-resizable-nw {
    left: -5px;
    top: -5px;
}
.ui-resizable-ne{
     top: -5px;
     right: -5px;
}
.ui-resizable-sw{
    bottom: -5px;
    left: -5px;
}
.ui-resizable-se{
     bottom: -5px;
    right:-5px;
}
.ui-resizable-n{
     top: -5px;
    left:50%;
}
.ui-resizable-s{
     bottom: -5px;
    left: 50%;
}
.ui-resizable-w{
     left:-5px;
     top:50%;
}
.ui-resizable-e{
     right:-5px;
     top:50%;
}

/*.ui-resizable-handle{
 z-index: 10010 !important;
}*/

</style>



