<template>

	<div class="bottom_bar">

      <div style="height:100% ; width: 83px ; background-color:#2F80ED ;  float: left;" > </div>
      <div style="height:100% ; width: 330px ; background-color:white ; float:left ; padding-left:30px" >
          <h3 style="font-size: 20px; ine-height: 28px; font-style: normal; font-weight: normal; margin-top: 19px; margin-bottom:5px"> {{ ml_state }} </h3>
<!--           <img id="cat" src="@/assets/img/dog.jpg">
 -->          <p style="color: #646464;"> 2000 images pre-processed. </p>
      </div>

      <div style="float:left ;; width: 200px ; height:100% ;">
          <p class="vertical-center" style="font-size:18px"> Elapsed time : {{msToTimeStr(ml_time_elapsed) }} </p>
      </div>

      <div style="float:left ; padding-right: 55px ; width: calc(100% - 1049px ); height:100% ;  ">

          <div style="margin-top:40px ;     height: 6px; background-color: #C4C4C4 ; " class="progress">
            <div class="progress-bar" role="progressbar" style="  background-color: #191414" aria-valuenow="10" aria-valuemin="0" aria-valuemax="100"  v-bind:style="{'width' : + ml_progress*100  +'%' }"   ></div>
          </div>

      </div>

       <div  style="float:left ; width: 205px; height:100% ; ">
          <!-- <button v-on:click="feat">Hola </button> -->
          <p class="vertical-center"  style="font-size:18px"> Estimated time : {{msToTimeStr(ml_time_estimated) }} </p>
      </div>

      <div style="height:100% ; width: 63px ;    float: left;" >
          <i  class="fa fa-pause vertical-center" style="margin-left: 13px; font-size:30px"></i>  <!-- nc-icon nc-button-pause -->
      </div>

      <div style="height:100% ; width: 73px ;   float: left;" >
          <i  class="fa fa-expand vertical-center" style="margin-left: 13px; font-size:25px"></i>
      </div>

	</div>

</template>

<script type="text/javascript">

import MLEngine from '@/ml_engine'

/*
states
loading_model
loading_data
waitng - wainting for data
extracting - extracting features
ready - ready to train
training
finished -  training
again_ready
*/

export default {
    name: 'BottomBar',
    data () {
        return {
            msg: '',
            ml_progress: 0.0,
            ml_time_elapsed: 0.0,
            ml_state: 'loading_model',
            auto_train: true,
            start_train: true // a flag which tells the worker to start trainnig
        }
    },

    computed: {
        ml_time_estimated: function () {
            return (1 - this.ml_progress) * ((this.ml_time_elapsed) / this.ml_progress) || 0
        }
    },

    methods: {

        msToTimeStr: function (s) {
            // Pad to 2 or 3 digits, default is 2
            function pad (n, z) {
                z = z || 2
                return ('00' + n).slice(-z)
            }

            var ms = s % 1000
            s = (s - ms) / 1000
            var secs = s % 60
            s = (s - secs) / 60
            var mins = s % 60
            var hrs = (s - mins) / 60

            if (hrs > 0) { return pad(hrs * 60 + mins, 3) + ':' + pad(secs) }

            return pad(hrs * 60 + mins) + ':' + pad(secs)
        },

        feat: function () {
            let items = this.$parent.$refs.gallery.gal_items
            let k1 = Object.keys(items)[0]
            MLEngine.get_feats(items[ k1 ].img)
        }

    }

}

</script>

<style type="text/css">

.bottom_bar {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 83px;

    display: block;
    z-index: 1;
    background-color: white;
    margin: 0;

  }

  .vertical-center {
  margin: 0;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}

.progress-bar{
  transition: none;
}

</style>



