<script>
import CameraImport from '@/components/CameraImport'

import GalleryItem2 from '@/components/GallleryItem2'
// import Modal from "@/components/Modal";
import DataMgr from '@/datamgr'
import FileUtils from '@/file_utils'


import { SimpleDropzone } from 'simple-dropzone';




import Compressor from 'compressorjs'
import blobToHash from 'blob-to-hash'

import Vue from 'vue'
import async from 'async'

export default {
    name: 'Gallery',
    components: {GalleryItem2, CameraImport },
    data () {
        return {
            msg: 'qqq', // ignore theis
            newTagInp: '', // the var for tghe new tag texxt boz
            display_mode: 'gallery', // gallery or table  
            sort_policy: 'time', // how to sort the igtems 
            newTagInpWheretoAdd: -1, // which item to add the new tag to
            tags: [], // list of all tags of the dataset
            mouse_selected_items: {}, // which items are select by mouse  
            mouse_selected_items_c: 0, // the cound of the mouse selected items 
            tag_selected: '', // which tag items should you displacy 
            type_selected : "training" , // which type of items are selected lol
            pagination_page: 0, // the index of pagionatino
            search_bar: '', // var for the search bar text box 
            next_key: 5, // the id of the next new gallery item which is to be added 
            N_PAGE: this.compute_n_cols(), // now many items to show per page
            expand_mode: false, // is the galery item expanded 
            expand_img: 0, // which image is the main full image in the expand mode 
            is_anyselected_visable: true, // a var which triggers the tool box ... as we dont want to show the delte change toolbox if the selected items are not visable 
            file_add_progress: 0, // the top progress bar which is used to show the file adding progress 
            selected_idx: [ ], // it tells which items you should show in the current vue 
            gal_items: { }, // 0: { img:"" ,  tag:undefined } , 1:{img : "" , tag:undefined , type:'training '} /// the main set of all the items 
            annotators: [] , // the varuable saving the annotator instance ,
            before_modal_state : {} , // save the state of the the thing so that close model predeves the state 

        }
    },

    computed: {

        count_tags: function () {
            var r = {}
            for (let tag of this.tags) {
                r[tag] = 0
            }
            for (let kk in this.gal_items) {
                var item = this.gal_items[kk]
                r[ item.tag ] += 1
            }
            return r
        }

    },

    mounted: function () {
        var that = this
        setInterval(function () { that.check_expended_deleted() }, 100)

        setInterval(function () { that.check_selected_item_not_in_view() }, 100)

        var that = this

        window.onresize = function () {
            that.N_PAGE = that.compute_n_cols()
        }

        setTimeout( function(){
            $('#exampleModal').on('hidden.bs.modal', function () {
                // do something…
                that.type_selected = "training"; 

                that.pagination_page = that.before_modal_state.pagination_page;
                that.expand_mode = that.before_modal_state.expand_mode;
                that.expand_img = that.before_modal_state.expand_img;
                

                that.update_gall();
                that.unselect_all_items();
            });
        } , 500)


        


        this.add_file_drag_listner_dropzone('drop-area' , 'hidden_file_input' );
        this.add_file_drag_listner_dropzone('dummy_drop_LOL' , 'hidden_file_input_folder' );
    },

  methods:
  {


    open_demo_modal : function(){

        this.before_modal_state.pagination_page = this.pagination_page  ;
        this.before_modal_state.expand_mode = this.expand_mode ;
        this.before_modal_state.expand_img = this.expand_img  ;

        this.pagination_page = 0;
        this.expand_mode  = true ; 
        this.expand_img = 0;


        this.type_selected='demo' ; 
        this.update_gall();
    },


     add_file_drag_listner_dropzone : function( drop_el_id, input_el_id  ){

        
        


        const dropEl = document.getElementById(drop_el_id) ;
        const inputEl = document.getElementById(input_el_id);
        const dropCtrl = new SimpleDropzone(dropEl, inputEl);
        let that = this;

        dropCtrl.on('drop', ({files}) => {
          console.log( files );
          let ret = [];

          for (let [path , file ] of files.entries()) {

              if( file.webkitRelativePath && file.webkitRelativePath.length > path.length )
                    path = file.webkitRelativePath;

              ret.push( {path:path , file : file });
          }

          console.log( ret);

          that.addNewImages_files(ret);
        });


     },

      //  add files from the drag and drop 
      add_file_drag_listner: function () {
          let dropArea = document.getElementById('drop-area')

              // for drad and drop
              ;['dragenter', 'dragover', 'dragleave', 'drop'].forEach(eventName => {
              dropArea.addEventListener(eventName, preventDefaults, false)
          })

          function preventDefaults (e) {
              e.preventDefault()
              e.stopPropagation()
          }

          dropArea.addEventListener('drop', handleDrop, false)
          var thatt = this

          function handleDrop (e) {
              let dt = e.dataTransfer
              // let files = dt.files

              FileUtils.traverseFileTree_from_dataTransfer( dt ).then(function( files ){
                 // console.log( files )
                 thatt.addNewImages_files(files);
              });


              
          }
      },

      // compute n entries for pagination
      compute_n_cols: function () {
          var w = window.innerWidth
          if (w < 576) { var n_col = 2 } else if (w < 868) { var n_col = 3 } else if (w < 1092) { var n_col = 4 } else if (w < 1400) { var n_col = 5 } else if (w < 1600) { var n_col = 6 } else if (w < 1900) { var n_col = 7 } else if (w < 2100) { var n_col = 8 } else if (w < 2400) { var n_col = 9 }

          n_col -= 1

          var nnn = 50 - 50 % n_col
          // alert( nnn + " " + n_col );
          return nnn
      },
      // v-click-outside="unclick_card_body"
      // when you click outside .. then unselect everything
      unselect_all_items () {
          var e = window.event

          if ((!this.expand_mode) && (e.shiftKey || e.metaKey || e.altKey || e.ctrlKey)) {
              return
          }

          for (var el of this.$children) {
              if (el.gal_id != undefined) {
                  if (el.just_selected) {
                  // alert("ju")
                  } else {
                      delete this.mouse_selected_items[el.gal_id ]
                      el.selected = false
                  }
              }
          }

          this.update_mouse_select_count()
      },

      // set the variable which displays the number of items selected by the mouse
      update_mouse_select_count () {
          var c = 0
          for (var k in this.mouse_selected_items) {
              if (this.mouse_selected_items[k]) {
                  if (this.gal_items[k]) {
                      c += 1
                  } else {
                      delete this.mouse_selected_items[k]
                  }
              }
          }

          this.mouse_selected_items_c = c
      },

      // when you click the change tag button for all selected items
      change_tag_button_click: function (e) {
          // this is to prevent the on click events so that they dont get disselected LOL
          e.preventDefault()
          // alert("fkkkk");
          $('#changeBlukTagBtn').addClass('open')
      },

      // change the tag of all the selected items
      changeTag_mouse_selected: function (tag) {
          for (var k in this.mouse_selected_items) {
              if (this.mouse_selected_items[k]) {
                  if (this.gal_items[k]) {
                      this.gal_items[ k ].tag = tag
                  }
              }
          }
          this.unselect_all_items()
          this.update_gall()
      },

      // delete the items selected by the mouse
      delete_mouse_selected: function () {
          var n = 0
          for (var k in this.mouse_selected_items) {
              if (this.mouse_selected_items[k]) { n += 1 }
          }

          if (n > 1 && (!confirm('Are you sure you want to delete ' + n + ' items?'))) {
              return
          }

          for (var k in this.mouse_selected_items) {
              if (this.mouse_selected_items[k]) {
                  delete this.gal_items[k]
                  delete this.mouse_selected_items[k]
                  this.update_gall()
                  // a hack to update the tags list lmao
                  this.tags.push('jj')
                  this.tags.pop()
              }
          }
      },

      // the new tag dialog box function
      newTag: function (tag) {

          tag = tag || '';

          tag = tag.trim()

          if (tag == '') { return }

          if (this.tags.includes(tag)) {
              return
          }
          this.tags.push(tag)
          return 1

          if (this.newTagInpWheretoAdd > -1) {
              this.gal_items[ this.newTagInpWheretoAdd ].tag = tag
          }
      },

      // remove the tag completely
      delete_tag: function (tag) {
          const index = this.tags.indexOf(tag)
          if (index > -1) {
              this.tags.splice(index, 1)
          }

          for (var k in this.gal_items) {
              if (this.gal_items[k].tag == tag) {
                  this.gal_items[k].tag = undefined
              }
          }
          this.update_gall()
      },

      // the inline new tag text box which appreas when adding tags to the mouse selectd items
      newTagMouseSelected: function () {
          this.newTag(this.newTagInp)
          if (this.newTagInp.trim() != '') {
              this.changeTag_mouse_selected(this.newTagInp.trim())
              this.newTagInp = ''
          }
      },

      // it updates the selected_idx ( representing the items to show ) .. sorts , filters etc acc to the options
      update_gall: function () {
          this.selected_idx = Object.keys(this.gal_items)
          var that = this

          this.selected_idx.sort(function (a, b) {
              // the compare fn
              // alert(this.sort_policy)
              if (that.sort_policy == 'tags') {
                  var t1 = that.gal_items[a].tag
                  var t2 = that.gal_items[b].tag
                  t1 = t1 || ''
                  t2 = t2 || ''
                  if (t1 < t2) {
                      return -1
                  } else if (t1 == t2) {
                      return Number(a) - Number(b)
                  } else {
                      return 1
                  }
              } else {
                  return Number(a) - Number(b)
              }
          })

          if (this.search_bar.length > 0) {
              this.selected_idx = this.selected_idx.filter(itt => ((this.gal_items[itt].tag || '').trim().toLowerCase().includes(this.search_bar.trim().toLowerCase())));
          } else {
              // do not do tag filter if search mode is on
              if (this.tag_selected != '') {

                  if( this.type_selected == "training"){
                      if (this.tag_selected == 'Untagged') {
                          this.selected_idx = this.selected_idx.filter(itt => (this.gal_items[itt].tag == undefined))
                      } else {
                          this.selected_idx = this.selected_idx.filter(itt => (this.gal_items[itt].tag == this.tag_selected))
                      }

                   }
                   
              } 

          }

          this.selected_idx = this.selected_idx.filter(itt => (this.gal_items[itt].type == this.type_selected  )) ; 

          this.selected_idx.push(0)
          this.selected_idx.pop()

          // this.selected_idx = this.selected_idx.slice(0, 20); // for debugging

          // alert(this.selected_idx);
      },

      filter_idx_tag: function (selected_idx, tag, brr = false) {
          var ret = []
          for (var i of selected_idx) {
              if (tag == '' && ((this.gal_items[i].tag == '') || (this.gal_items[i].tag == undefined))) {
                  ret.push(i)
              } else if (tag == this.gal_items[i].tag) {
                  ret.push(i)
              }

              if (brr && ret.length >= 20) { break }
          }
          return ret
      },

      paginate_idx: function (selected_idx, page) {
          return selected_idx.slice(this.N_PAGE * page, (page + 1) * this.N_PAGE)
      },

      get_n_pages: function (selected_idx) {
          return Math.ceil(selected_idx.length / this.N_PAGE)
      },

      can_increase_page: function () {
          var max_pages = Math.ceil(this.selected_idx.length / this.N_PAGE)
          if (this.pagination_page + 1 >= max_pages) { false } else { return true }
      },

      can_decrease_page: function () {
          if (this.pagination_page == 0) { false } else { return true }
      },

      increase_page: function (xx) {
          if (this.can_increase_page()) { this.pagination_page += 1 }
      },

      decrease_page: function (xx) {
          if (this.can_decrease_page()) { this.pagination_page -= 1 }
      },

      // do stuff if the expanded item is deleted
      check_expended_deleted: function () {
          if (!(this.gal_items[ this.expand_img])) {
              if (this.selected_idx.length > 0) { this.expand_img = this.selected_idx[0] } else { this.expand_mode = false }
          }
      },

      // hide the toolbox if the selected item is not present in the screen
      check_selected_item_not_in_view: function () {
          function isScrolledIntoView (elem) {
              var docViewTop = $(window).scrollTop()
              var docViewBottom = docViewTop + $(window).height()

              var elemTop = $(elem).offset().top
              var elemBottom = elemTop + $(elem).height()

              return ((elemBottom - 200 <= docViewBottom) && (elemTop + 200 >= docViewTop))
          }

          if (!('$' in window)) { return }
          if ($('.card.selected').length == 0) { return }
          this.is_anyselected_visable = false
          for (var el of $('.card.selected')) {
              if (isScrolledIntoView(el)) {
                  this.is_anyselected_visable = true
                  break
              }
          }
      },







      // given a list of file objects , add those! 
      addNewImages_files: function (files, tag = undefined) {
           

          if (tag == undefined && this.tag_selected != '' && this.tag_selected != 'Untagged') {
              if( this.type_selected == "training")
                  tag = this.tag_selected
          }

          let type = this.type_selected;



          var that = this
          var n_prog = 0
          that.file_add_progress = 0

          // for( var i=0 ; i< files.length ; i++ ){
          //     this.addNewImages_one(  files[i] , tag );
          // }

          async.eachSeries(files, function (file, callback) {

              // meaning it came from the dir upload   
              if( file.file ){
                let path = file.path;
                file = file.file;
                if( path && that.type_selected != "demo"  ){
                    let tag_from_path = path.split('/')  // todo check if this works with widnows 
                    tag_from_path = tag_from_path[ tag_from_path.length -2  ] || "";
                    if(tag_from_path.length > 0  )
                        tag = tag_from_path;
                    }
                
              }

              that.addNewImages_one(file, tag , type , callback)
              n_prog += 1
              that.file_add_progress = n_prog / files.length
          }, function () {
              if( type=='demo' && that.type_selected == 'demo')
              {
                // in the demo mode just select the newly added image as well!!! 
                that.expand_img =  that.next_key;
              }
          })
      },

      // bulk add new images from file dialog box
      addNewImages: function (event) {
          this.addNewImages_files(event.target.files)
      },

      addImgB64: function (base64data, tag, type ,  cb   ) {
          var img_hash = Math.random().toString()
          DataMgr.image_b64[img_hash] = base64data
          console.log('sax' + img_hash)
          this.next_key += 1
          this.gal_items[ this.next_key ] = Vue.observable({tag: tag, img: img_hash , 'type': type   })
          this.update_gall()
          cb()
      },

      // add a single image from dialog box
      addNewImages_one: function (file, tag , type , cb) {
          var thatt = this

          if (!file) {
              return cb()
          }

         

          new Compressor(file, {
              quality: 0.6,
              maxWidth: 300,
              maxHeight: 300,
              success (blob) {
                  var reader = new FileReader()
                  reader.readAsDataURL(blob)

                  reader.onloadend = function () {
                      var base64data = reader.result
                      thatt.addImgB64(base64data, tag , type , cb)

                       if( ! ( tag in thatt.tags) ){
                            thatt.newTag( tag);
                       }


                  }

                  reader.onerror = function () {
                      cb()
                  }

                  reader.onabort = function () {
                      cb()
                  }
              },
              error (err) {
                  console.log(err.message)
                  cb()
              }
          })
      }

  }
}

</script>

<style type="text/css">

.has-search .form-control {
    padding-left: 2.375rem;
}

.has-search .form-control-feedback {
    position: absolute;
    z-index: 2;
    display: block;
    width: 2.375rem;
    /*height: 2.375rem;*/
    line-height: 2.375rem;
    text-align: center;
    pointer-events: none;
    color: #aaa;
}

.search_input::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: #191414;
  opacity: 0.8; /* Firefox */
}

.more_botton{
  background: #FFFFFF;
  box-shadow: 0px 0px 1.94759px rgba(40, 41, 61, 0.04), 0px 3.89518px 7.79036px rgba(96, 97, 112, 0.16);
  border-radius: 23.3711px ! important;
}

.btn_disable > i {
  color: #d6d6de !important;
}

.btn_disable {
  box-shadow: none;
}

.top-progress-bar {
    background: linear-gradient(to right, #3E7BFA var(--scroll), transparent 0);
    background-repeat: no-repeat;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    height: 4px;
    z-index: 1000000;
}

  @media (min-width: 576px){
    .gal_resp {
        -ms-flex: 0 0 50%;
        -webkit-box-flex: 0;
        flex: 0 0 50%;
        max-width: 50%;
    }}

  @media (min-width: 868px){
    .gal_resp{
        -ms-flex: 0 0 33.333333%;
        -webkit-box-flex: 0;
        flex: 0 0 33.333333%;
        max-width: 33.333333%;
    }}

  @media (min-width: 1092px){
    .gal_resp {
        -ms-flex: 0 0 25%;
        -webkit-box-flex: 0;
        flex: 0 0 25%;
        max-width: 25%;
    }}

  @media (min-width: 1400px){
    .gal_resp {
        -ms-flex: 0 0 20%;
        -webkit-box-flex: 0;
        flex: 0 0 20%;
        max-width: 20%;
    }}

  @media (min-width: 1600px){
    .gal_resp {
        -ms-flex: 0 0 16.6667%;
        -webkit-box-flex: 0;
        flex: 0 0 16.6667%;
        max-width: 16.6667%;
    }}

    @media (min-width: 1900px){
    .gal_resp {
        -ms-flex: 0 0 14.2857142857%;
        -webkit-box-flex: 0;
        flex: 0 0 14.2857142857%;
        max-width: 14.2857142857%;
    }}

  @media (min-width: 2100px){
    .gal_resp {
        -ms-flex: 0 0 12.5%;
        -webkit-box-flex: 0;
        flex: 0 0 12.5%;
        max-width: 12.5%;
    }}

  @media (min-width: 2400px){
    .gal_resp {
        -ms-flex: 0 0 11.1111111111%;
        -webkit-box-flex: 0;
        flex: 0 0 11.1111111111%;
        max-width: 11.1111111111%;
    }}

</style>



