


function readEntriesAsync(reader) {
    return new Promise((resolve, reject) => {
        reader.readEntries(entries => {
            resolve(entries);
        }, error => reject(error));
    })
}

function get_file_sync (item ) {
    return new Promise((resolve, reject) => {
        item.file(entries => {
            resolve(entries);
        }, error => reject(error));
    })
}


// https://developer.mozilla.org/en-US/docs/Web/API/DataTransferItem/webkitGetAsEntry 



// map the folder drag and drop dataT object to list of files
async function traverseFileTree_from_dataTransfer( dataTransfer ){
	let ret = [];
	let items = dataTransfer.items;

    for (var i=0; i < items.length; i++) {
      ret = ret.concat ( await traverseFileTree(items[i].webkitGetAsEntry())) ;
    }

    return ret; 
}



// async fuction to convert the dir to 
async function traverseFileTree(item, path) {

  // console.log("wuhan 11 ")
  path = path || "";
  if (item.isFile) {

    let file = await get_file_sync( item );
    // console.log("File wohan :", path + file.name);
    return  [ {file : file , path : path + file.name } ];
    

  } else if (item.isDirectory) {
    // Get folder contents
    var dirReader = item.createReader();

    let ret = [];

    while( true ){
    	let entries = await readEntriesAsync( dirReader );// dirReader.readEntries();

    	console.log( entries.length )

    	if( entries.length == 0)
    		break;

	    for (var i=0; i<entries.length; i++) {
	       ret = ret.concat( await traverseFileTree(entries[i], path + item.name + "/")) ;
	    }
    }

    

    console.log("re")
    console.log( ret );

    return ret; 


  }
  else{
    console.log("else ")
  }
}






const FileUtils = {
	traverseFileTree : traverseFileTree , 
	traverseFileTree_from_dataTransfer:traverseFileTree_from_dataTransfer , 

}
export default FileUtils


