<template>
<div class="sideBaru">

  <div class="sidebar_wrapper" style="max-height:calc(100% - 203px);overflow: auto;">

      <br>
     <input v-model="proj_name" style="  font-family: Rubik;font-style: normal; font-weight: normal;color:#191414; font-size:26px; border:none; background: transparent; text-align: center; max-width: 200px;" class="navbar-brand prjName" href="#pablo"></input>
     <br>
     <br>

      <div style="margin-bottom: 1px;" class="card no_border">

          <span class="dropdown">
              <button data-toggle="dropdown"  style="width:80% ; margin-right:10%"  type="submit" class=" btn  btn-fill pull-right purpleButttum buttonBorder">  Import Data <i style="    margin-left: 13px;" class="fa fa-plus"></i></button>

              <div style="width:100%" class="dd-menu allign_left" aria-labelledby="dropdownMenuButton">

                    <span onclick='$("#hidden_file_input").click();' class="dropdown-item" style="cursor:pointer; padding-top:20px;padding-bottom:20px" >
                        <i style="    margin-left: 13px;" class="fa fa-file"></i><a class="dropdown-item" style=" width:unset; display:unset; " v-on:click="" href="#">From Files</a>
                    </span>

                    <span onclick='$("#hidden_file_input_folder").click();' class="dropdown-item" style="cursor:pointer; padding-top:20px;padding-bottom:20px" >
                        <i style="    margin-left: 13px;" class="fa fa-file"></i><a class="dropdown-item" style=" width:unset; display:unset; " v-on:click="" href="#">From Folder</a>
                    </span>

                    <span v-on:click="$parent.$refs.gallery.$refs.camera_modal.$modal.show('camera_modal');" class="dropdown-item" style="cursor:pointer; padding-top:20px;padding-bottom:20px" >
                        <i style="    margin-left: 13px;" class="fa fa-camera"></i><a class="dropdown-item" style=" width:unset; display:unset; " v-on:click="" href="#">Camera</a>
                    </span>

              </div>

           </span>

      </div>

      <div class="card no_border" style="padding:5px" >

           <h4 style="padding-left:15px" class="sidebar-title">PRIMARY TAGS</h4>

                   <div style="cursor: pointer; width:150px"  v-on:click="$parent.$refs.gallery.$modal.show('new_tag_modal');" class="sidebar_newbutton">
                   NEW TAG <i style="margin-left:25px;" class="fa fa-plus"></i>
                  </div>

                  <div style="margin:8px;"> </div>

                  <span style="cursor: pointer;"  >
                   <div   class="dropdown"> <h5 v-on:click="unselect_all " v-bind:class="[hakkkk && ($parent.$refs.gallery.tag_selected == '') ? 'selecteddd' : '']"    class="sidebar_tag_select card-title sidebar_text">All<span style="float: right ; font-size:13px;">

                    </span>   </h5>  </div>  <div style="margin:5px;"> </div>

                  </span>

                  <span style="cursor: pointer;"  >
                   <div   class="dropdown"> <h5 v-on:click=" select_tag('Untagged') " v-bind:class="[hakkkk && ($parent.$refs.gallery.tag_selected == 'Untagged') ? 'selecteddd' : '']"    class="sidebar_tag_select card-title sidebar_text">Untagged<span style="float: right ; font-size:13px;">

                    </span>   </h5>  </div>  <div style="margin:5px;"> </div>

                  </span>

                  <span style="cursor: pointer;"  v-for="(tag, index) in $parent.$refs.gallery.tags" >
                   <div   class="dropdown"> <h5  v-on:click=" select_tag(tag) " v-bind:class="[hakkkk && ($parent.$refs.gallery.tag_selected == tag) ? 'selecteddd' : '']"  class="sidebar_tag_select card-title sidebar_text"><span class="the_text_of_the_tag" >{{tag}}</span> <span style="float: right ; font-size:13px;">
                           <i   v-on:click="dont_select=true;"  style="color:rgb(134, 142, 150)" class="fa fa-ellipsis-v" data-toggle="dropdown" ></i>
                           <div class="dd-menu allign_right" aria-labelledby="dropdownMenuButton">
                                <a class="dropdown-item"  v-on:click="delete_tag(tag)" href="#">Delete</a>
                          </div>
                    </span>   </h5>  </div>  <div style="margin:5px;"> </div>

                  </span>
                  <!-- <h5 class="card-title">Cat <span style="float: right ; font-size:13px;">3 items</span> </h5> <hr> -->
                  <!-- <h5 class="card-title">Bird <span style="float: right ; font-size:13px;">0 items</span> </h5> -->

          <!-- <div class="card-footer "> -->

              <!-- <hr> -->

          <!-- </div> -->
      </div>

      <div class="card no_border" style="padding:5px" >

         <h4 style="padding-left:15px" class="sidebar-title">SORT BY</h4>

         <span style="cursor: pointer;"  >
           <div   class="dropdown"> <h5 v-on:click="$parent.$refs.gallery.sort_policy = 'time'; $parent.$refs.gallery.pagination_page = 0; hakkkk+=1 ; $parent.$refs.gallery.search_bar = ''; $parent.$refs.gallery.update_gall(); " v-bind:class="[hakkkk && ($parent.$refs.gallery.sort_policy == 'time') ? 'selecteddd' : '']"    class="sidebar_tag_select card-title sidebar_text">Time<span style="float: right ; font-size:13px;">

            </span>   </h5>  </div>  <div style="margin:5px;"> </div>

          </span>

          <span style="cursor: pointer;"  >
             <div   class="dropdown"> <h5 v-on:click="unselect_all() ; $parent.$refs.gallery.sort_policy = 'tags'; $parent.$refs.gallery.pagination_page = 0; hakkkk+=1 ; $parent.$refs.gallery.search_bar = ''; $parent.$refs.gallery.update_gall(); " v-bind:class="[hakkkk && ($parent.$refs.gallery.sort_policy == 'tags') ? 'selecteddd' : '']"    class="sidebar_tag_select card-title sidebar_text">Tags<span style="float: right ; font-size:13px;">

              </span>   </h5>  </div>  <div style="margin:5px;"> </div>

            </span>

      </div>

  </div>

  <div class="msgBaru">

      <!-- <hr> -->

       97% of your images are predicted correctly. 3% incorrectly.

  </div>
  <div class="msgBarBar"></div>

</div>

</template>

<script>

import Vue from 'vue'

export default {
    name: 'Sidebar',
    data () {
        return {
            msg: '',
            selected_items: Vue.observable({ }),
            console: console,
            hakkkk: 2,
            proj_name: 'Project Name',
            alertt: alert,
            dont_select: false // hack so that when clicking the drop icon does not select the tag
        }
    },
    methods: {
        unselect_all: function (tag) {
        // for( var k  in this.selected_items)
        //     this.selected_items[k]=false;
        // this.hakkkk += 1;
            this.$parent.$refs.gallery.tag_selected = ''
            this.$parent.$refs.gallery.update_gall()
            this.$parent.$refs.gallery.pagination_page = 0
            this.$parent.$refs.gallery.expand_mode = false
        },

        select_tag: function (tag) {
            if (this.dont_select) {
                var thatt = this
                setTimeout(function () {
                    thatt.dont_select = false
                }, 30)
                return
            }

            this.$parent.$refs.gallery.unselect_all_items()

            this.$parent.$refs.gallery.tag_selected = tag
            this.hakkkk += 1
            this.$parent.$refs.gallery.update_gall()
            this.$parent.$refs.gallery.pagination_page = 0
            this.$parent.$refs.gallery.expand_mode = false

            this.$parent.$refs.gallery.search_bar = ''
        },

        delete_tag: function (tag) {
            this.selected_items[tag] = false
            var thatt = this
            setTimeout(function () { thatt.$parent.$refs.gallery.tag_selected = '' }, 100)
            this.$parent.$refs.gallery.delete_tag(tag)
        },
        add_class_drop_hack: function (tag) {
        // alert("jjj")
        // $("#d-"+ss).addClass("open")
            this.selected_items[tag] = !this.selected_items[tag]
        },
        perform_click: function (a) {
            $(a).click()
        }
    }
}

</script>

<style type="text/css">

  .sideBaru{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 260px;
    display: block;
    z-index: 1;
    background-color: white;
  }

  .msgBaru{
    position: absolute;
    /*top: 0;*/
    bottom: 83px;
    left: 0;
    width: 100%;
    height: 120px;
    display: block;
    z-index: 1;
    background-color: #27AE60;

    padding: 20px;
    font-size: 19px;
    font-weight: 500px;
    color: white;
  }

  .msgBarBar{
    background-color: rgba(255,255,255,0.6);
    width: 140px;
    position: absolute;
    left: 60px;
    height: 3.5px;
    bottom: 193px;
    z-index: 100;

  }

  .sidebar_wrapper{
    padding: 20px;

  }

    .sidebar_tag_select{
        padding:10px ;

        border-radius: 10px;
    }

    .sidebar_tag_select:hover {
        background-color:  #a9a9a985
    }

    .sidebar_tag_select.selecteddd {
        color:white;
        background-color: #3E7BFA;
    }

    .sidebar-title{
      font-family: Rubik;
      font-style: normal;
      font-weight: 300;
      font-size: 15px;
      line-height: 17px;

      color: #191414;
    }

    .sidebar_text{
       font-weight: 500 ! important;
       font-size: 18px;

    }

    .selecteddd > span > i{
       color: white ! important;
    }

    .sidebar_newbutton{
       font-family: Rubik;
      font-style: normal;
      font-weight: 500;
      font-size: 14.5px;
      line-height: 24px;
      /* identical to box height, or 171% */

      /* Dark / Dark 0 */

      color: #1C1C28;
      background: #F1F1F1;
      padding: 10px 20px;
      border-radius: 9.4px;

    }

</style>



